<template>
  <el-tabs v-model="activeName" :stretch="true" @tab-click="handleClick">
    <el-tab-pane label="" name="first"></el-tab-pane>
    <el-tab-pane label="" name="second"></el-tab-pane>
    <el-tab-pane label="" name="third"></el-tab-pane>
    <el-tab-pane label="" name="fourth"></el-tab-pane>
    <el-tab-pane label="" name="five"></el-tab-pane>
    <el-tab-pane label="" name="six"></el-tab-pane>
    <el-tab-pane label="" name="seven"></el-tab-pane>
  </el-tabs>
</template>

<script>
export default {
    data() {
         return {
             activeName: 'second',
         }
    },
    methods: {
        handleClick(tab, event) {
            console.log(tab, event);
          }
    }
}
</script>


<style>
.el-tabs__item {
    padding: 0 10px;
}
</style>



