<template>
  <div> 
    <div class="register-wrapper"> 
      <div id="register">
        <p class="title"></p>
        <el-form
          :model="ruleForm2"
          status-icon
          :rules="rules2"
          ref="ruleForm2"
          label-width="0"
          class="demo-ruleForm"
        >
          <el-form-item prop="tel">
            <el-input v-model="ruleForm2.tel" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="pass">
            <el-input type="password" v-model="ruleForm2.pass" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="checkPass">
            <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')" style="width:100%;"></el-button>
            <p class="login" @click="gotoLogin"></p>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script> 

export default {
  name: "Register", 
  data() {
    // <!---->
    let checkTel = (rule, value, callback) => {
      if ( value === '' ) {
        callback( new Error('') )
      } else if ( !this.checkMobile(value) ) {
        callback( new Error('') )
      } else {
        callback()
      }
    }
    // //  <!---->
    // let checkSmscode = (rule, value, callback) => {
    //   if (value === '') {
    //     callback(new Error(''))
    //   } else {
    //     callback()
    //   }
    // }

  // 
    let validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""))
      } else {
        if (this.ruleForm2.checkPass !== "") {
          this.$refs.ruleForm2.validateField("checkPass");
        }
        callback()
      }
    }
    // <!---->
    let validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.ruleForm2.pass) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {  
      toastInstance: null, 
      ruleForm2: {
        pass: "",
        checkPass: "",
        tel: "",
        // smscode: ""
      },
      rules2: {
        pass: [{ validator: validatePass, trigger: 'change' }],
        checkPass: [{ validator: validatePass2, trigger: 'change' }],
        tel: [{ validator: checkTel, trigger: 'change' }],
        // smscode: [{ validator: checkSmscode, trigger: 'change' }],
      },
      // buttonText: '',
      // isDisabled: false, // 
      // flag: true
    }
  }, 
  methods: {
    getList(str, time){//,Ajax
        //Toast
        this.show(str, time);
        setTimeout(() => {
            //Toast
            this.toastInstance.close()
        }, time);
    },
    show(str, time) {
          this.toastInstance = Toast({
              message: str,
              duration: time, //-1
              position: 'middle',
              iconClass: 'glyphicon glyphicon-heart',
              className: 'myToast'  //css
          });
    },
    // <!---->
    submitForm(formName) {
      this.getList("", 1000); //
      this.$refs[formName].validate(valid => {
        if (valid) {
          // setTimeout(() => {
          //   // 
          // }, 400);
          this.$notify({
                  title: '',
                  message: '',
                  duration: 3000
              });
        } else {
          this.$notify({
                  title: '',
                  message: '',
                  duration: 3000
              });
          return false;
        }
      })
    },
    // <!---->
    gotoLogin() {
      this.$router.push({
        path: "/login"
      });
    },
    // 
    checkMobile(str) {
      let re = /^1\d{10}$/
      if (re.test(str)) {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>

<style scoped>
.loading-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: #aedff8;
  display: flex;
  align-items: center;
  justify-content: center;
}
.register-wrapper img {
  position: absolute;
  z-index: 1;
}
.register-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
#register {
  max-width: 340px;
  margin: 60px auto;
  background: #fff;
  padding: 20px 40px;
  border-radius: 10px;
  position: relative;
  z-index: 9;
}
.title {
  font-size: 26px;
  line-height: 50px;
  font-weight: bold;
  margin: 10px;
  text-align: center;
}
.el-form-item {
  text-align: center;
}
.login {
  margin-top: 10px;
  font-size: 14px;
  line-height: 22px;
  color: #1ab2ff;
  cursor: pointer;
  text-align: left;
  text-indent: 8px;
  width: 160px;
}
.login:hover {
  color: #2c2fd6;
}
.code >>> .el-form-item__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.code button {
  margin-left: 20px;
  width: 140px;
  text-align: center;
}
.el-button--primary:focus {
  background: #409EFF;
  border-color: #409EFF;
  color: #fff;
}
</style>



